<template>
  <div id="app" >
    <p @click = "setDate" ></p>
    <p>{{data}}</p>
    <p @click = "setDate2" >2015-2-20</p>
    <p>{{data2}}</p>



  </div>
</template>



<script>
import './css/style.scss'

export default {
  name: 'app',
  data () {
    return {
        data:'',
        data2:''
    }
  },

  methods:{

   setDate(){

       this.$calendar.show({
           onOk: (date) =>{
              this.data = date
           }
       });

    },
    setDate2(){

        this.$calendar.show({
            year:[1925,2015],  //,
            date:'2015-2-20',  //
            onOk: (date) =>{
                this.data2 = date
            },
            onCancel:()=>{
                console.log('')
            }
        });

    },

  },
}
</script>



